<template>
  <div class="match">
    <div class="header">
      <span class="a jiesan" v-if="userType==1" @click="showModal=1">解散战队</span>
      <div class="cont">
        <div class="title">{{myteamName}}</div>
        <router-link to="/list" class="find__btn"></router-link>
      </div>
      <router-link to="/rule" tag="span" class="a rule">赛事规则</router-link>
    </div>
    <div class="team my">
      <div class="tit">我的战队</div>
      <ul class="plays" v-if="myTeamTree.length>0">
        <li v-for="(a,ai) in myTeamTree" :key="ai" @click="handleUser(a,ai)">
          <div class="base" :class="{'leader':a.type==1,'add':userType==1 && a.status==1,'matched':a.status==2}">
            <div class="kickout" v-if="userType==1 && a.status==2 && a.can_del==1" @click="del_Person(a.del_id)"></div>
          </div>
          <div class="name">{{a.name | filterName}}</div>
        </li>
      </ul>
    </div>
    <div class="no__match" v-if="matchStatus!=3">
      <div class="a" v-show="matchStatus==1">暂未匹配战队</div>
      <div class="b" v-show="matchStatus==2">
        <img src="~img/matching_icon_matching.png">
        <p>对战申请中</p>
      </div>
    </div>
    <!-- 匹配成功 -->
    <div class="team other" v-if="matchStatus==3">
      <div class="tit">{{otherTeamName}}</div>
      <ul class="plays" v-if="otherTeamTree.length>0">
        <li v-for="(b,bi) in otherTeamTree" :key="bi">
          <div class="base" :class="{'leader':b.type==1,'matched':b.status==2}"></div>
          <div class="name">{{b.name}}</div>
        </li>
      </ul>
    </div>
    <div class="bottom">
      <div class="m__ing" v-if="btnStatus!=4">
        <!-- 禁用申请 -->
        <div class="btn btn__apply--dis" v-show="userType==1 && btnStatus==0"></div>
        <!-- 可申请 -->
        <div class="btn btn__apply--can" v-show="userType==1 && btnStatus==1" @click="apply">申请对战</div>
        <!-- 取消申请 -->
        <div class="btn btn__cancel" v-show="userType==1 && btnStatus==2" @click="cancelApply"></div>
        <!-- 队员可退出 -->
        <div class="btn btn__signout" v-show="userType==2" @click="showModal=6"></div>
      </div>
      <div class="m__success" v-if="btnStatus==4">
        <div class="text">
          <div class="item">
            <div class="lt">比赛时间：</div>
            <div class="rt">{{applicationData.isdate | filterInfo | dateFmt}}</div>
          </div>
          <div class="item">
            <div class="lt">比赛地点：</div>
            <div class="rt">{{applicationData.gameAddress | filterInfo}}</div>
          </div>
        </div>
        <!-- <div class="cancel__btn" v-if="userType==1"></div> -->
      </div>
    </div>
    <transition name="cover">
      <div v-show="showModal" class="mask" key="a" @touchmove.prevent>
        <!-- 解散战队 -->
        <div v-show="showModal==1" class="box dissolution">
          <div class="title">解散战队</div>
          <div class="cont">
            <div class="txt">
              <p>解散战队将无法恢复数据信息</p>
              <p>是否确定解散？</p>
            </div>
          </div>
          <div class="btns">
            <div class="btn btn__dissolution" @click="dissolution"></div>
            <div class="btn btn__cancel_02" @click="showModal=0"></div>
          </div>
        </div>
        <!-- 踢出好友 -->
        <div v-show="showModal==2" class="box kickout">
          <div class="title">提示</div>
          <div class="cont">
            <div class="txt">
              <p>是否将好友请出战队？</p>
            </div>
          </div>
          <div class="btns">
            <div class="btn btn__kickout" @click="kickout"></div>
            <div class="btn btn__cancel_02" @click="showModal=0"></div>
          </div>
        </div>
        <!-- 邀请队员 -->
        <div v-show="showModal==3" class="box invitation">
          <div class="title">邀请队员</div>
          <div class="cont">
            <Input v-model="phone" type="text" placeholder="请输入队员手机号" size="large" clearable :maxlength="11" />
            <!-- <input type="text" placeholder="请输入队员手机号" v-model="phone" maxlength="11"> -->
            <div class="err">{{errTxt}}</div>
          </div>
          <div class="btns">
            <div class="btn btn__cancel" @click="cancelInvitation"></div>
            <div class="btn btn__invitation" @click="invitation"></div>
          </div>
        </div>
        <!-- 暂无赛事 -->
        <div v-if="showModal==4" class="box zanwu">
          <div class="title">提示</div>
          <div class="cont">
            <div class="txt">
              <p>暂无赛事，请关注公众号比赛动态</p>
            </div>
          </div>
          <div class="btns">
            <div class="btn btn__sure_02" @click="showModal=0"></div>
          </div>
        </div>
        <!-- 参加赛事 -->
        <div v-if="showModal==5" class="box join">
          <div class="title">{{ssData.gamename}}</div>
          <div class="cont">
            <div class="join__txt">
              <p>未到报名时间</p>
              <!-- <p>已报名8支战队/共24支战队</p> -->
            </div>
            <div class="join__date">
              <p>报名时间：</p>
              <p>{{ssData.fromdate | dateFmt }}至{{ssData.todate | dateFmt }}</p>
            </div>
          </div>
          <div class="btns">
            <div class="btn btn__cancel" @click="showModal=0"></div>
            <div class="btn btn__join" @click="join"></div>
          </div>
        </div>
        <!-- 退出战队 -->
        <div v-show="showModal==6" class="box signout">
          <div class="title">提示</div>
          <div class="cont">
            <div class="txt">
              <p>是否确定退出战队？</p>
            </div>
          </div>
          <div class="btns">
            <div class="btn btn__signout"></div>
            <div class="btn btn__cancel_02" @click="showModal=0"></div>
          </div>
        </div>
      </div>
    </transition>
    <Spin size="large" fix v-if="spinShow"></Spin>
  </div>
</template>
<script>
const Interval1 = 5000; //邀请状态
const Interval2 = 5000; //申请状态轮询

import mixin from '../../mixins/mixins.js';
import { Local, Datefmt, Exp } from 'js/utils.js';
// let DefaultMyTeam = [{
//     type: 1, //类型1团长模式、2团员模式
//     status: 0, //1可添加('base')、2已邀请('matched')
//     can_del:0,//0不可删除、1可删除
//     del_id:'',
//     name: '团长'
//   },
//   {
//     type: 2, //类型
//     status: 1, //1可添加、2已匹配 
//     can_del:0,//0不可删除、1可删除
//     del_id:'',
//     name: ''
//   },
//   {
//     type: 2, //类型
//     status: 1, //1可添加、2已匹配
//     can_del:0,//0不可删除、1可删除
//     del_id:'',
//     name: ''
//   },
//   {
//     type: 2, //类型
//     status: 1, //1可添加、2已匹配
//     can_del:0,//0不可删除、1可删除
//     del_id:'',
//     name: ''
//   },
//   {
//     type: 2, //类型
//     status: 1, //1可添加、2已匹配
//     can_del:0,//0不可删除、1可删除
//     del_id:'',
//     name: ''
//   }
// ]

// let DefaultOtherTeam = [{
//     type: 1, //类型1团长模式、2团员模式
//     status: 0,
//     name: '团长'
//   },
//   {
//     type: 2, //类型
//     status: 1,
//     name: ''
//   },
//   {
//     type: 2, //类型
//     status: 1,
//     name: ''
