export default {
  name: 'match',
  props: [],
  data() {
    return {
      userId: Local.get('user_id') || '',
      teamId: Local.get('team_id') || '',
      userType: 2, //1团长模式、2团员模式(默认)
      myTeamTree: Local.get('team') || this.renderTeamTree(1),
      otherTeamTree: this.renderTeamTree(2),
      myteamName: '王者荣耀战队',
      otherTeamName: '天龙八部战队',
      matchStatus: 1, //1暂未匹配、2匹配中、3匹配成功
      btnStatus: Local.get('btn_status') || 0, //0不可申请、1可以申请、2取消对战申请、3退出、4取消对战
      showModal: 0, //1解散、2踢出提示、3邀请、4暂无赛事提示、5报名信息、6退出提示
      addIndex: 1, //邀请好友索引
      phone: '', //邀请人手机号
      errTxt: '',
      delId: '', //删除团员id
      ssData: {
        gamename: ''
      }, //有赛事内容
      applicationData: '', //申请成功
      timer1: null, //团员邀请轮询
      timer2: null, //申请状态轮询
      spinShow: true
    };
  },
  mixins: [mixin],
  components: {

  },
  created() {
    let that = this
    that.checkStatus();
    //首次获取一次
    that.getTeamListInfo(); //获取团队列表

  },
  mounted() {
    let that = this
    //首次获取一次
    that.getTeamStatus(); //获取团队状态
    if (!that.timer2) {
      that.timer2 = setInterval(() => {
        that.getTeamStatus(); //获取团队状态
      }, Interval2)
    }
  },
  destroyed() {
    let that = this;
    if (that.timer1) { //如果定时器在运行则关闭
      clearInterval(that.timer1);
      that.timer1 = null;
    }
    if (that.timer2) { //如果定时器在运行则关闭
      clearInterval(that.timer2);
      that.timer2 = null;
    }
  },
  computed: {

  },
  filters: {
    filterName(value) {
      return !value || value == '' ? '无' : value;
    },
    filterInfo(value) {
      return !value || value == '' || value == null ? '待定' : value;
    },
    dateFmt(value) {
      return Datefmt(new Date(value), 'YYYY-MM-DD hh:mm:ss');
    },
  },
  methods: {
    checkStatus() {
      let that = this;
      let phone = Local.get('user_phone');
      if (phone && phone != '') {
        //判断信息是否完善
        that.$api.SelectUserInfo(phone).then(res => {
          if (Number(res.code) == 10000) {
            let data = res.data;
            // console.log(data)
            if (!data.userInfo.phone || data.userInfo.phone == '') {
              that.$Message.success('请先登录');
              Local.clear();
              setTimeout(() => {
                that.$router.push('/login');
              }, 500)
              return;
            }

            if (data.userInfo.username == '' || data.userInfo.schoolname == '' || data.userInfo.major == '' || data.userInfo.classname == '' || data.userInfo.studentcode == '') {
              that.$Message.success('请先完善个人信息');
              setTimeout(() => {
                that.$router.push('/fillinfo');
              }, 500)
              return;
            }
          } else {
            that.$Message.warning(res.msg);
          }
        })
      } else {
        that.$Message.success('请先登录');
        setTimeout(() => {
          that.$router.push('/login');
        }, 500)
      }
    },
    /**
     * 渲染战队树
     */
    renderTeamTree(type) {
      let that = this;
      let arr = [];
      if (type == 1) {
        for (let i = 0; i < 5; i++) {
          let obj = {};
          obj.type = i == 0 ? 1 : 2; //1团长、2团员
          obj.status = i == 0 ? 0 : 1; //1可添加('base')、2已邀请('matched')
          obj.can_del = 0; //0不可删除、1可删除
          obj.del_id = '';
          obj.name = '';
          arr.push(obj);
        }
      } else if (type == 2) {
        for (let i = 0; i < 5; i++) {
          let obj = {};
          obj.type = i == 0 ? 1 : 2;
          obj.status = i == 0 ? 0 : 1;
          obj.name = '';
          arr.push(obj);
        }
      }
      return arr;
    },
    /**
     * 获取团队状态(是否已匹配，团员人数)
     */
    getTeamStatus() {
      let that = this;
      // console.log(that.teamId)
      that.$api.GetTeamStatus(that.teamId).then(res => {
        // console.log(res)
        //申请成功
        if (res.data.flag) {
          that.matchStatus = 2; //显示对战申请中icon
          that.btnStatus = 2; //显示取消申请按钮
          that.changeDelStatus(0); //全部置为不可删除
          that.applicationData = res.data;
          if (res.data.teamid && res.data.teamid != '') {
            clearInterval(that.timer2);
            that.timer2 = null;
            that.getTeamListInfo(res.data.teamid, 2)
          }
        }
      })

    },
    /**
     * 获取团队列表
     */
    getTeamListInfo(teamId, type = 1) {
      let that = this;
      let id = teamId || that.teamId;
      that.$api.SelectTeamUsers(id).then(res => {
        that.spinShow = false;
        // console.log(res)
        //团长
        let tz = res.data.filter((item) => {
          return item.id == item.asTeam.userid;
        })
        //团员（有团队）
        let tyhasTeam = res.data.filter((item) => {
          return item.id !== item.asTeam.userid && (item.asTeam.id && item.asTeam.id != null && item.asTeam.id != '');
        })

        //团员（无团队）
        let tyNoTeam = res.data.filter((item) => {
          return item.id !== item.asTeam.userid && (!item.asTeam.id || item.asTeam.id == null || item.asTeam.id == '');
        })

        //列表重组
        let list = [...tz, ...tyhasTeam, ...tyNoTeam];

        switch (type) {
          case 1:
            //我队
            // console.log(list)
            that.myteamName = tz[0].asTeam.teamname; //团队名称

            list.forEach(function(item, index) {
              let obj = that.myTeamTree[index];
              /**
               * 权限判断
               * item.asTeam.userid==登录账号id
               */
              if (item.asTeam.userid == that.userId) {
                that.userType = 1; //1团长模式、2团员模式
              }

              /**
               * 有队员
               */
              if (item.id !== item.asTeam.userid && (item.asTeam.id && item.asTeam.id != null && item.asTeam.id != '')) {
                that.$set(obj, 'status', 2); //已邀请
                that.$set(obj, 'can_del', 1); //可删除
              }
              that.$set(obj, 'type', item.id == item.asTeam.userid ? 1 : 2); //1团长、2团员
              that.$set(obj, 'del_id', item.autrId);
              that.$set(obj, 'name', item.username);
            })
            break;
          case 2:
            //其他队
            // console.log(res)
            // 
            that.otherTeamName = tz[0].asTeam.teamname; //团队名称
            that.matchStatus = 3; //3匹配成功
            that.btnStatus = 4;
            that.changeDelStatus(0);
            list.forEach(function(item, index) {
              let otherObj = that.otherTeamTree[index];
              that.$set(otherObj, 'name', item.username);
              if (item.id == item.asTeam.userid) {
                that.$set(otherObj, 'type', 1); //1团长
              } else {
                that.$set(otherObj, 'type', 2); //2团员
                that.$set(otherObj, 'status', 2); //已匹配
              }
            })
            break;
          default:
            break
        }
      })
    },
    /**
     * 添加、删除操作
     */
    handleUser(item, index) {
      let that = this;
      that.addIndex = index;
      if (that.userType == 1) {
        if (item.status == 1) {
          //可添加
          // console.log('可添加')
          that.showModal = 3;
        } else if (item.status == 2) {
          //可删除
          // console.log('可删除')
        }
      }
    },
    del_Person(delId) {
      let that = this;
      that.showModal = 2;
      that.delId = delId;
    },
    /**
     * 踢出成员
     */
    kickout() {
      let that = this;
      let obj = that.myTeamTree[that.addIndex];
      // if (that.addIndex == 4) {
      //   console.log('最后一个')
      // } else if (that.addIndex == 1) {
      //   console.log('第二个')
      // }

      if (that.delId) {
        that.$api.DeleteTeamUser(that.delId, that.teamId).then(res => {
          // console.log(res)
          if (Number(res.code) == 10000) {
            that.$Message.success('操作成功');
            setTimeout(() => {
              that.$set(obj, 'status', 1); //可添加
              that.$set(obj, 'can_del', 0); //不可删除
              that.$set(obj, 'name', '');
              that.showModal = 0;
              that.btnStatus = 0; //禁用申请按钮
              that.getTeamListInfo();
            })
          } else {
            that.$Message.warning(res.msg);
          }
        })
        // Local.set('team', that.myTeamTree);
        // Local.set('btn_status', that.btnStatus);
      }
    },
    /**
     * 取消邀请
     */
    cancelInvitation() {
      let that = this;
      that.showModal = 0;
      that.phone = '';
    },
    /**
     * 邀请成员
     */
    invitation() {
      let that = this;
      if (!that.phone) {
        that.errTxt = '*请输入手机号';
        return;
      } else if (!/^1\d{10}$/.test(that.phone)) {
        that.errTxt = '*请输入正确手机号';
        return;
      } else {

        that.$api.AddTeamUser(that.phone, that.teamId).then(res => {
          // console.log(res)
          if (Number(res.code) == 10000) {
            that.errTxt = '';
            that.phone = '';
            that.showModal = 0;
            that.$Message.info({
              content: '邀请已发出，等待成员加入',
              duration: 10
            });
            //轮询查询成员列表
            if (that.timer1) { //如果定时器在运行则关闭
              clearInterval(that.timer1);
              that.timer1 = null;
            }
            if (!that.timer1) {
              that.timer1 = setInterval(() => {
                that.getTeamListInfo(); //获取团队列表
              }, Interval1)
            }
          } else {
            // that.$Message.warning(res.msg);
            that.errTxt = '*' + res.msg;
          }
        })
        // setTimeout(() => {
        //   let obj = that.myTeamTree[that.addIndex];
        //   that.$set(obj, 'status', 2);//2已邀请
        //   that.$set(obj, 'can_del', 1);//可删除
        //   that.$set(obj, 'name', that.phone);
        //   that.errTxt = '';
        //   that.phone = '';
        //   // that.errTxt = '*好友未注册或输入错误';
        //   that.showModal = 0;
        //   Local.set('team', that.myTeamTree);
        //   // Local.set('btn_status', that.btnStatus);
        // }, 10)
      }
    },
    /**
     * 申请对战
     */
    apply() {
      let that = this;
      that.matchStatus = 2; //显示对战申请中icon
      that.btnStatus = 2; //显示取消申请按钮
      // // 模拟ajax
      // setTimeout(() => {
      //   //判断是否有未结束的赛事
      //   //1无赛事,2有赛事,3有值
      //   switch (3) {
      //     case 1:
      //       that.showModal = 4; //显示无赛事弹层
      //       that.matchStatus = 1; //1暂未匹配战队
      //       that.btnStatus = 1; //重置为可申请
      //       // Local.set('btn_status', that.btnStatus);
      //       break;
      //     case 2:
      //       //有赛事
      //       that.showModal = 5;
      //       that.matchStatus = 1; //1暂未匹配战队
      //       that.btnStatus = 1; //重置为可申请
      //       // Local.set('btn_status', that.btnStatus);
      //       break;
      //     case 3:
      //       that.matchStatus = 3; //3匹配成功
      //       that.btnStatus = 4;
      //       //申请成功隐藏删除按钮（置为不可删除）
      //       that.myTeamTree.forEach(function(obj, index) {
      //         if (index != 0) {
      //           that.$set(obj, 'can_del', 0);
      //         }
      //       })
      //       that.otherTeamTree.forEach(function(obj, index) {
      //         if (index == 0) {
      //           that.$set(obj, 'name', '团长');
      //         } else {
      //           that.$set(obj, 'status', 2);
      //           that.$set(obj, 'name', 'xxx');
      //         }
      //       })
      //       break;
      //     default:
      //       break;
      //   }
      // }, 1000)

      that.$api.CreateApplication(that.teamId).then(res => {
        // console.log(res)
        switch (Number(res.code)) {
          case 90011:
            that.showModal = 4; //显示无赛事弹层
            that.matchStatus = 1; //1暂未匹配战队
            that.btnStatus = 1; //重置为可申请
            break;
          case 90012:
            //有赛事
            that.showModal = 5;
            that.matchStatus = 1; //1暂未匹配战队
            that.btnStatus = 1; //重置为可申请
            that.ssData = res.data; //赛事内容
            break;
          case 10000:
            //申请成功
            that.$Message.info({
              content: '申请成功，等待审核',
              duration: 1
            });
            // that.matchStatus = 2; //显示对战申请中icon
            // that.btnStatus = 2; //显示取消申请按钮
            // that.changeDelStatus(0); //全部置为不可删除
            that.getTeamStatus();
            break;
            // case 3:
            //   that.matchStatus = 3; //3匹配成功
            //   that.btnStatus = 4;
            //   //申请成功隐藏删除按钮（置为不可删除）
            //   // that.changeDelStatus(0); //全部置为不可删除
            //   // that.otherTeamTree.forEach(function(obj, index) {
            //   //   if (index == 0) {
            //   //     that.$set(obj, 'name', '团长');
            //   //   } else {
            //   //     that.$set(obj, 'status', 2);
            //   //     that.$set(obj, 'name', 'xxx');
            //   //   }
            //   // })
            //   break;
          default:
            break;
        }

      })
      // Local.set('btn_status', that.btnStatus);
    },
    /**
     * 取消对战申请
     */
    cancelApply() {
      let that = this;
      // Local.set('btn_status', that.btnStatus);
      if (that.applicationData) {
        let id = that.applicationData.applicationId;
        that.$api.DeleteApplication(id).then(res => {
          // console.log(res)
          if (Number(res.code) == 10000) {
            that.$Message.success('操作成功');
            setTimeout(() => {
              that.matchStatus = 1; //1暂未匹配战队
              that.btnStatus = 1;
              that.changeDelStatus(1); //全部置为可删除
            }, 500)
          } else {
            that.$Message.warning(res.msg);
          }
        })
      }
    },
    /**
     * 参加赛事
     */
    join() {
      let that = this;
      that.showModal = 0;
      that.apply();
    },
    /**
     * 解散战队
     */
    dissolution() {
      let that = this;
      that.$api.DeleteTeamInfo(that.teamId).then(res => {
        // console.log(res)
        if (Number(res.code) == 10000) {
          that.$Message.success('操作成功');
        } else {
          that.$Message.warning(res.msg);
        }
      })
    },
    changeDelStatus(status) {
      let that = this;
      if (Exp.isArray(that.myTeamTree) && that.myTeamTree.length) {
        that.myTeamTree.forEach(function(obj, index) {
          if (index != 0) {
            that.$set(obj, 'can_del', status); //0全部置为不可删除,1全部置为可删除
          }
        })
      }
    }
  },
  watch: {
    myTeamTree: {
      handler: function(list) {
        let that = this;
        //查看够5人则可点击申请对战
        let canApplay = list.slice(1).every(function(item) {
          return item.status == 2;
        })
        if (canApplay) {
          clearInterval(that.timer1);
          that.timer1 = null;
        }
        // matchStatus   1暂未匹配、2匹配中、3匹配成功
        if (canApplay && that.matchStatus == 1) {
          that.btnStatus = 1; //显示可申请按钮(可删除)
        }
        // matchStatus   2匹配中、3匹配成功 不可删除
        if (that.matchStatus == 2 || that.matchStatus == 3) {
          that.changeDelStatus(0);
        }
      },
      immediate: true,
      deep: true
    },
    btnStatus(value) {
      // Local.set('btn_status', value);
    }
  }
};

</script>
<style lang="scss" scoped>
