      transform: translateY(-50%);
      font-size: 14px;
      font-weight: 500;
      text-decoration: underline;

      &.jiesan {
        left: 0;
        color: #4F525B;
      }

      &.rule {
        right: 0;
        color: #C7AE67;
      }
    }

    .cont {
      .title {
        font-size: 19px;
        font-weight: bold;
        color: #FDF2C6;
        line-height: 19px;
        text-align: center;
      }

      .find__btn {
        display: block;
        width: 140px;
        height: 37px;
        margin-top: 10px;
        transition: all .2s ease-in-out 0s;
        background: url('~img/competition_btn_lssg.png') center center / 100% 100% no-repeat;

        &:active {
          opacity: .5;
          transform: scale(1.1);
        }
      }
    }
  }

  .team {
