      top: 23%;
    }

    &.other {
      top: 58%;
    }

    .tit {
      font-size: 19px;
      font-weight: bold;
      color: #FDF2C6;
      line-height: 19px;
      text-align: center;
    }

    .plays {
      display: flex;
      justify-content: space-between;
      // border: 1px solid #fff;

      li {
        width: 50px;

        &:nth-of-type(2n) {
          margin-top: 40px;
        }

        &:nth-of-type(2n+1) {
          margin-bottom: 40px;
        }


        .base {
          position: relative;
          background-size: 100% 100%;
          background-position: center center;
          background-repeat: no-repeat;
          width: 50px;
          height: 56px;
          background-image: url('~img/matching_icon_ty02.png');

          //踢出
          .kickout {
            width: 22px;
            height: 22px;
            position: absolute;
            top: 0.5px;
            right: 1.5px;
            background: url('~img/matching_btn_kickout.png') center center / 100% 100% no-repeat;
          }

          //团长
          &.leader {
            background-image: url('~img/matching_icon_tz.png');
          }


          //可添加
          &.add {
            background-image: url('~img/matching_btn_addty.png');
          }

          //已匹配
          &.matched {
            background-image: url('~img/matching_icon_ty01.png');
          }
        }

        .name {
          font-size: 12px;
          font-weight: 500;
          color: #958F9D;
          line-height: 19px;
          text-align: center;
          overflow: hidden;
          text-overflow: ellipsis;
          -ms-text-overflow: ellipsis;
          white-space: nowrap;
        }
      }
    }
  }

  //未匹配
  .no__match {
    position: absolute;
    top: 62%;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100px;
    height: 100px;

    //暂未匹配
    .a {
      font-size: 14px;
      font-weight: bold;
      color: #958F9D;
      line-height: 19px;
    }

    //匹配中
    .b {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;

      @keyframes rotate {
        from {
          transform: rotate(0deg)
        }

        to {
          transform: rotate(360deg)
        }
      }

      img {
        width: 36px;
        height: 36px;
        animation: rotate 3s linear infinite;
      }


      p {
        font-size: 14px;
        font-weight: bold;
        color: #FDF2C6;
        line-height: 19px;
        margin-top: 10px;
      }
    }
  }


  .bottom {
    position: absolute;
    left: 0;
    bottom: 30px;
    width: 100%;
    // border: 1px solid #fff;

    .m__ing {
      width: 100%;

      .btn {
        width: 313px;
        height: 53.5px;
        margin: 0 auto;
        background-size: 100% 100%;
        background-position: center center;
        background-repeat: no-repeat;
        transition: all .2s ease-in-out 0s;

        &:active {
          opacity: .5;
          // transform: scale(1.1);
        }

        &__apply {
