          }
        }

        &__signout {
          background-image: url('~img/matching_btn_signout.png');
        }

        &__cancel {
          background-image: url('~img/matching_btn_cancel.png');
        }
      }

    }

    .m__success {
      display: flex;
      justify-content: space-between;
      padding-left: 15px;

      .text {
        font-size: 12px;
        font-weight: 500;
        color: #958F9D;

        .item {
          display: flex;
          line-height: 19px;

          .rt {
            max-width: 150px;
          }
        }
      }

      .cancel__btn {
        width: 128px;
        height: 46px;
        background: url('~img/matching_btn_qxdz.png') center center / 100% 100% no-repeat;
        transition: all .2s ease-in-out 0s;

        &:active {
