      .title {
        font-size: 16px;
        font-weight: bold;
        color: #ACA4C9;
        margin-bottom: 10px;
        text-align: center;
      }

      .cont {
        margin-top: 10px;

        .txt {
          color: #958F9D;
          font-size: 14px;
          font-weight: 500;
          line-height: 21px;
